import React from "react";
import { Routes } from "react-router-dom";
import AuthorizedRoute from "./AuthorizedRoute";

const Roles = React.lazy(() => import("../settings/Roles"));
const Role = React.lazy(() => import("../settings/role"));
const Identities = React.lazy(() => import("../settings/Identities"));
const Identity = React.lazy(() => import("../settings/Identity"));
const Authentications = React.lazy(() => import("../settings/Authentications"));
const Authentication = React.lazy(() => import("../settings/Authentication"));
const AccessControls = React.lazy(() => import("../accessControl/accessControls"));
const Protect = React.lazy(() => import("../protect/protect"));
const ProtectConfig = React.lazy(() => import("../protect/protectConfig"));

export default function SecurityRoute() {
  return (
    <Routes>
      <AuthorizedRoute path="roles" element={<Roles />} />
      <AuthorizedRoute path="identities" element={<Identities />} />
      <AuthorizedRoute path="authentication" element={<Authentications />} />
      <AuthorizedRoute path="accessControls" element={<AccessControls />} />
      <AuthorizedRoute path="roles/:id" element={<Role />} />
      <AuthorizedRoute path="authentication/:id" element={<Authentication />} />
      <AuthorizedRoute path="identities/:id" element={<Identity />} />
      <AuthorizedRoute path="protect" element={<Protect />} />
      <AuthorizedRoute path="protect/config/:id" element={<ProtectConfig />} />
    </Routes>
  );
}
